.book-header-img {
	height: 8rem;
	width: 100%;
	object-fit: cover;
	z-index: -5;
}

.book-title {
	font-size: 2rem;
	background: rgba(210,210,210,1.0);
	padding: 0.5rem 2rem;
	border-top-right-radius: 6px;
}
.book-title-div {
	display: inline-block;
	transform: translateY(-3.5rem);
	position: absolute;
	top: 8rem;
	left: 0;
}

.book-box-img {
	display: flex;
	flex-direction: column;
	width: 50rem;
	border-radius: 0;
	margin-top: 0;
	box-shadow: 3px 3px 8px rgba(170,170,170,1.0);
	position: relative;
	margin-bottom: 2rem;
}


.sign-in-container.no-card {
	align-items: flex-start;
	text-align: center;
}

.book-detail {
	height: 100%;
	border: 1px solid black;
}

.book-subtitle {
	font-size: 1.5rem;
	width: 98%;
	margin: 0 1% 0 1%;
}

.input-horiz-container {
	display: flex;
	width: 100%;
	justify-content: space-between;
	flex-direction: column;
}

.description-field {
	border: none;
	height: 6rem;
	border-radius: 3px;
	padding: 0.5rem 0.5rem;
	resize: none; 
}
.description-field.worker {
	background: rgba(188,190,192,0.5);
	height: 8rem;
}

.input-side.horiz-flex {
	flex-direction: row;
	width: 100%;
	flex-wrap: wrap;
	align-items: flex-end;
}
.input-side.horiz-flex > .input-group {
	width: 100%;
}

.description-field:focus {
	outline: none;
}

@media (min-width: 550px) {
	.input-horiz-container {
		flex-direction: row;
	}
	.input-side.horiz-flex > .input-group {
		width: 47%;
	}
}

