.manage-container {
	display: flex;
	justify-content: center;
}

.manage-content {
	display: flex;
	flex-direction: column;
	width: 100%;
	overflow: scroll;
	margin-bottom: 2rem;
	border-bottom-right-radius: 6px;
	border-bottom-left-radius: 6px;
}

.manage-header {
	background: #3f6bb4;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem;
	color: white;
	background-image: url('../images/joesbackgroundgrey.png');
	background-position: center;
  background-size: cover;
}
.manage-title {
	font-size: 2.5rem;
}
.manage-btn {
	border: none;
	border-radius: 3px;
	padding: 0.5rem 1.5rem;
	background: white;
	color: #3f6bb4;
	margin-top: 1rem;
	font-size: 1.3rem;
	cursor: pointer;
}

.chart-header {
	font-size: 1.5rem;
	color: black;
}
.chart-desc {
	font-size: 1.1rem;
	margin-bottom: 0.5rem;
}

.schedule-divider {
	border-top: 1px solid rgba(188,190,192,1);
	width: 100%;
	margin-top: 1.5rem;
	margin-bottom: 0.75rem;
}

.scheduled-container {
	display: flex;
	flex-direction: column;
}
.filter-desc {
	margin: 0.75rem 0.1rem;
	font-size: 0.9rem;
}

.schedule-chart {
	border: 2px solid rgba(188,190,192,0.5);
	width: 100%;
	height: 30rem;
	border-bottom-right-radius: 6px;
	border-bottom-left-radius: 6px;
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	max-width: 100%;
	overflow-x: hidden;
}
.schedule-heading {
	width: 100%;
	display: flex;
}
.column-head {
	border: 2px solid rgba(188,190,192,0.5);
	border-right: none;
	width: 33.33333%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0.5rem;
	border-bottom: none;
}
.left-column {
	border-top-left-radius: 6px;
}
.right-column {
	border-top-right-radius: 6px;
	display: none;
	border-right: 2px solid rgba(188,190,192,0.5);
}
.flex-column {
	border-top-right-radius: 6px;
	border-right: 2px solid rgba(188,190,192,0.5);
}
.blue-footer {
	width: 100%;
	height: 4rem;
	background: #3f6bb4;
}

.job-row {
	width: 100%;
	height: 3.5rem;
	min-height: 3.5rem;
	display: flex;
	background: white;
}
.job-row:hover,
.job-row:active,
.job-row:focus {
	background: rgba(188,190,192,0.7);
}

.row-divider {
	border-top: 1px solid rgba(188,190,192,0.7);
	width: 100%;
	margin: 0;
}

.row-item {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 33.3333%;
	text-align: center;
}
.right-row {
	display: none;
}
.chevron {
	fill: rgba(188,190,192,1);
}

.jobs-filter {
	display: flex;
	width: 100%;
	justify-content: space-between;
	border: 2px solid rgba(188,190,192,0.7);
	border-radius: 6px;
}
.jobs-filter-row {
	display: flex;
	padding: 0.75rem 0.1rem;
	justify-content: center;
	align-items: center;
	width: 33.33333%;
	border-right: 2px solid rgba(188,190,192,0.7);
	font-size: 0.7rem;
	cursor: pointer;
	color: rgba(0,0,0,0.65);
}
.jobs-filter-row:hover {
	background: #3f6bb4;
	color: white;
}
.manage-jobs-body {
	padding: 1rem;
}
.numbers-div {
	display: flex;
	width: 100%;
	justify-content: flex-end;
	height: 0;
}
.number-bubble {
	width: 1rem;
	height: 1rem;
	background: red;
	margin-right: calc(33.33333333% - 1rem);
	transform: translate(0.4rem, -0.4rem);
	display: flex;
	justify-content: center;
	align-items: center;
	color: white;
	font-size: 0.5rem;
	border-radius: 50%;
}
.number-bubble.worker {
	margin-right: calc(25% - 1rem);
}

/* Larger than mobile */
@media (min-width: 400px) {}

/* Larger than phablet */
@media (min-width: 550px) {
	.column-head {
		width: 20%;
	}
	.row-item {
		width: 20%;
	}
	.right-column {
		width: 40%;
		display: flex;
	}
	.right-width {
		width: 100%;
	}
	.right-row {
		width: 40%;
		display: flex;
		padding-left: 24px;
	}
	.flex-column {
		border-top-right-radius: 0px;
		border-right: none;
	}
	.manage-content {
		width: 50rem;
		border: 1px solid #3f6bb4;
	}
	.jobs-filter-row {
		font-size: 1rem;
	}
}

