.dashboard-container {
	display: flex;
	align-items: center;
	flex-direction: column;
	margin-bottom: 1rem;
}
.dashboard-head-title {
	width: 100%;
	margin: 1.25rem 0 0.7rem 0;
	font-size: 1.3rem;
	padding: 0 1rem;
}
.dashboard-head-title.admin {
	width: 100%;
	padding: 0 1.5rem;
	max-width: 100rem;
}
.dashboard-box {
	display: flex;
	width: 100%;
	padding: 0 1rem;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: space-between;
}
.dashboard-box.admin {
	width: 100%;
	padding: 0 1.5rem;
	max-width: 100rem;
}
.dashboard-side {
	display: flex;
	background: rgba(188,190,192,0.5);
	flex-direction: column;
	justify-content: center;
	border-radius: 3px;
	width: calc(100% - 0rem);
	box-shadow: 2px 2px 5px grey;
	margin: 0.5rem 0;
	height: 19rem;
}
.dashboard-divider {
	border-right: 2px solid rgba(188,190,192,0.5);
	width: 0px;
}
.job-confirmation {
	font-size: 2rem;
	text-align: center;
	line-height: 2rem;
	margin-bottom: 1rem;
}
.smaller-job-confirmation {
	font-size: 1.5rem;
	padding: 0 1rem;
}
.dashboard-title {
	width: 100%;
	text-align: center;
	font-size: 2rem;
	font-weight: 500;
	padding: 0.5rem 1rem;
}
.text-calendar-block {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.calendar-block {
	display: flex;
	justify-content: flex-start;
	padding-left: 1rem;
}
.calendar-div {
	display: flex;
	flex-direction: column;
	background: white;
	align-items: center;
	border-radius: 10px;
	width: 100%;
}
.calendar-div.dashboard {
	width: 10rem;
	height: 10rem;
}
.date-text {
	font-size: 5rem;
	line-height: 6.5rem;
}
.month-text {
	font-size: 2.3rem;
	line-height: 3.7rem;
}
.date-text.dashboard {
	font-size: 4.5rem;
}
.calendar-text {
	flex-grow: 1;
	background: red;
	width: 100%;
	color: white;
	font-weight: 500;
	display: flex;
	align-items: center;
	justify-content: center;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
}
.calendar-date {
	flex-grow: 3;
	border-radius: 5px;
	display: flex;
	align-items: center;
}
.job-details-btn {
	margin: 1rem 1rem;
	border: none;
	height: 2rem;
	border-radius: 3px;
	padding: 0.1rem 0.5rem;
	background: #3f6bb4;
	color: white;
	font-size: 0.9rem;
	cursor: pointer;
}

.book-now-container {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	flex-direction: column;
	height: 65%;
	width: 100%;
	padding: 1.5rem;
	padding-top: 0.75rem;
}
.book-now-btn,
.book-now-btn:hover {
	width: 60%;
	background: #3f6bb4;
	color: white;
	border-radius: 3px;
	padding: 0.1rem 0.5rem;
	height: 2rem;
	border: none;
	font-size: 1rem;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
}
.book-now-desc {
	margin-bottom: 0.75rem;
	margin-top: -0.8rem;
	font-size: 1rem;
}
.dashboard-icon-div {
	display: flex;
	align-items: center;
}
.dashboard-icon {
	background: #3f6bb4;
	width: 30%;
	height: 19rem;
	min-height: 9rem;
	border-top-left-radius: 3px;
	border-bottom-left-radius: 3px;
	fill: white;
	padding: 0.5rem;
}
.dashboard-icon-text {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-size: 5vw;
	font-weight: 500;
	padding: 0.5rem 0 0.5rem 1.5rem;
}
.worker-head-profile {
	height: 3rem;
	width: 3rem;
	margin-right: 1.5rem;
	object-fit: cover;
	border-radius: 50%;
}
.vertical-divider {
	border-right: 1px solid black;
}
.book-img {
	height: 35%;
	object-fit: cover;
}
.cust-dash-pic {
	display: flex;
	justify-content: center;
	align-items: center;
	background-image: url('../images/raking.jpeg');
	background-position: center;
  background-size: cover;
}
.cust-dash-pic > img {
	width: 11rem;
	height: auto;
}

/* Larger than mobile */
@media (min-width: 400px) {}

/* Larger than phablet */
@media (min-width: 550px) {
	
}

/* Larger than tablet */
@media (min-width: 750px) {
	.dashboard-box {
		width: 50rem;
		flex-direction: row;
		padding: 0;
	}
	.dashboard-head-title {
		width: 50rem;
		padding: 0;
	}
	.dashboard-side {
		width: calc(50% - 0.5rem);
		display: flex;
		flex-direction: column;
	}
	.dashboard-icon-text {
		font-size: 1.5rem;
	}
}

