.account-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.account-box {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
}
.account-sidebar {
	width: 25%;
	border-right: 1px solid rgba(188,190,192,0.5);
	display: none;
	flex-direction: column;
	min-height: 15rem;
}
.account-small-sidebar {
	width: 100%;
	display: flex;
	flex-direction: column;
}
.sidebar-row {
	padding: 0.5rem 1rem;
	font-size: 1.2rem;
	border-bottom: 1px solid rgba(188,190,192,0.5);
	cursor: pointer;
	color: rgba(0, 0, 0, 0.65);
}
.sidebar-row:hover {
	color: #3f6bb4;
}
.account-body {
	width: 100%;
}
.account-header {
	text-align: left;
	display: flex;
	flex-direction: column;
	padding: 1rem;
	border-bottom: 1px solid rgba(188,190,192,0.5);
	width: 100%;
}
.account-title {
	font-size: 2rem;
}
.account-title-div {
	display: flex;
	justify-content: space-between;
}
.account-subtitle {
	font-size: 1.4rem;
}
.sign-out-box {
	display: flex;
	padding: 0.5rem 1.5rem;
	align-items: center;
	border-bottom: 1px solid rgba(188,190,192,0.5);
}
.signout-lbl {
	width: 50%;
	margin: 0;
}
.basic-info-box {
	display: flex;
	padding: 0.5rem 1.5rem;
	flex-direction: column;
	padding-bottom: 1rem;
	width: 100%;
}
.account-input > .input-side {
	margin: 0;
}
.account-input > .input-side > .input-group > .text-field {
	background: rgba(188,190,192,0.5);
	color: black;
}
.account-password-box > .input-group > .text-field {
	background: rgba(188,190,192,0.5);
	color: black;
}
.account-password-box > .input-group {
	width: 100%;
}
.account-password-box {
	display: flex;
	flex-direction: column;
	padding: 0.5rem 1.5rem;
	padding-bottom: 1rem;
}
.sign-in-btn.account {
	width: 100%;
	margin-top: 0.6rem;
}
.sign-in-btn.account-full {
	margin-top: 0.6rem;
}
.sign-in-btn:disabled {
	background: rgba(188,190,192,0.9);
}
.text-message-div {
	display: flex;
	align-items: center;
}
.input-side.full {
	width: 100%;
}
.payment-form {
	background: rgba(188,190,192,0.5);
	padding: 1.5rem;
	width: 100%;
}
.read-card-container {
	margin-top: 0.5rem;
}
.read-card {
	display: flex;
	font-size: 1rem;
	justify-content: space-between;
}
.card-btn-container {
	display: flex;
	justify-content: space-between;
}
.cancel-card-btn {
	background: red;
}
.card-btn {
	width: 48%;
}
.stripe-powered-img {
	height: 2.5rem;
}
.flex-between {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

/* Larger than mobile */
@media (min-width: 400px) {}

/* Larger than phablet */
@media (min-width: 550px) {
	.account-container {
		margin: 1.5rem 0;
	}
	.account-password-box > .input-group {
		width: 50%;
	}
	.sign-in-btn.account {
		width: 50%;
	}
}

/* Larger than tablet */
@media (min-width: 750px) {
	.account-box {
		width: 50rem;
		border: 1px solid rgba(188,190,192,0.5);
	}
	.account-header {
		text-align: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 1rem 0;
		border-bottom: 1px solid rgba(188,190,192,0.5);
		width: 100%;
	}
	.account-small-sidebar {
		display: none;
	}
	.account-sidebar {
		display: flex;
	}
	.account-body {
		width: 75%;
	}
}

/* Larger than desktop */
@media (min-width: 1000px) {}

/* Larger than Desktop HD */
@media (min-width: 1200px) {}